
// variables
$main-color: #FFFFFF;
$hover: scale(1.5);
// mixin
%text-setup {
  color: $main-color;
  opacity: 0.9;
  text-shadow: 0px 4px 12px rgba(0, 0, 0, 0.12);
}



html {
  min-height: 100%;
}

body {
  width: 100%;
  height: 100vh;
  padding: 0;
  margin: 0;
  overflow: hidden;
  background: url('../src/img/el-alce-web-njX7_Ixraio-unsplash.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  position: fixed;


}

body::before {
  content: '';
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  position: absolute;
  background-color: rgba(65, 30, 117, 0.808)
}

// fadeIn animation
@keyframes fadeIn {

  from {
    opacity: 0;
  }


  to {
    opacity: 1;
  }

  10% {
    opacity: 0.1;
  }

  20% {
    opacity: 0.2;

  }

  30% {
    opacity: 0.3;

  }

  40% {
    opacity: 0.4;

  }

  50% {
    opacity: 0.5;

  }

  60% {
    opacity: 0.6;

  }

  70% {
    opacity: 0.7;

  }

  80% {
    opacity: 0.8;

  }
}

.fadeIn {
  animation-name: fadeIn;
  animation-duration: 1.4s;

}